.overlay-wrapper {
  width: 100%;

  .pwa-iphone-clock-padding {
    background-color: #333;
    height: 60px;
    width: 100%;
    z-index: 1000;
    position: fixed;
  }
}

.overlay {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: #ddd;
  z-index: -1;
  filter: blur(8px);
  -webkit-filter: blur(8px);
  -moz-filter: blur(8px);
  -o-filter: blur(8px);
  -ms-filter: blur(8px);
}
